.modal-home .login {
  box-sizing: border-box;
  background-image: url(../../static/media/BG.f6da57f5.png);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
}
.modal-home .login .inputBox {
  padding: 0 0.3rem;
}
.modal-home .login .inputBox .Ipt {
  position: relative;
}
.modal-home .login .inputBox .Ipt .iconBox {
  position: absolute;
  left: 0.4rem;
  top: 0.4rem;
  width: 0.35rem;
}
.modal-home .login .inputBox .Ipt .iconBox img {
  width: 100%;
  height: 100%;
}
.modal-home .login .footer {
  padding: 0.3rem 0.3rem;
  display: flex;
  justify-content: space-between;
}
.modal-home p {
  text-align: center;
  line-height: 1rem;
  font-size: 0.35rem;
}
.modal-home .registered {
  box-sizing: border-box;
  background-image: url(../../static/media/BG.f6da57f5.png);
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
}
.modal-home .registered .inputBox {
  padding: 0 0.3rem;
}
.modal-home .registered .inputBox .Ipt {
  position: relative;
}
.modal-home .registered .inputBox .Ipt .iconBox {
  position: absolute;
  left: 0.4rem;
  top: 0.4rem;
  width: 0.35rem;
}
.modal-home .registered .inputBox .Ipt .iconBox img {
  width: 100%;
  height: 100%;
}
.modal-home .registered .inputBox .codeIpt {
  position: relative;
}
.modal-home .registered .inputBox .codeIpt .codeNumber {
  margin: 0;
  padding: 0.3rem 0.2rem 0.3rem 0.8rem;
}
.modal-home .registered .inputBox .codeIpt span {
  margin-left: 0.2rem;
  color: #fff;
  padding: 0 0.2rem;
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  background: -webkit-gradient(linear, left top, right top, from(#9718ec), to(#68c7e4));
  background: linear-gradient(to right, #9718ec, #68c7e4);
}
.modal-home .registered .inputBox .codeIpt .iconBox {
  position: absolute;
  left: 0.4rem;
  top: 0.23rem;
  width: 0.35rem;
}
.modal-home .registered .inputBox .codeIpt .iconBox img {
  width: 100%;
  height: 100%;
}
.modal-home .inputBox {
  width: 100%;
}
.modal-home .inputBox input {
  width: 100%;
  margin: 0.2rem 0;
  border: 1px solid #aaa;
  border-radius: 0.5rem;
  padding: 0.3rem 0.2rem 0.3rem 0.8rem;
  border-top-color: #38b6e7;
  border-bottom-color: #38b6e7;
  border-left-color: #9718ec;
  border-right-color: #9718ec;
}
.modal-home .am-button {
  padding: 0;
  margin: 0;
  width: 100%;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  border-bottom-left-radius: 0.2rem;
  border-bottom-right-radius: 0.2rem;
  color: #fff;
  font-weight: 800;
  background: -webkit-gradient(linear, left top, right top, from(#9718ec), to(#68c7e4));
  background: linear-gradient(to right, #9718ec, #68c7e4);
}
.modal-home .am-button::before {
  border: none !important;
}
.modal-home .am-button:active {
  background: #ddd;
}
.modal-home .am-button:active {
  background: #ddd;
}
