.am-modal-mask {
  background-color: rgba(255, 255, 255, 0) !important;
}
.am-modal-close,
.am-modal-title {
  margin-top: 0.15rem;
}
.playerModal {
  margin-bottom: 0.9rem;
}
.playerModal .video-detail-list-view {
  height: 100% !important;
}
.playerModal .video-detail-list-view .am-list-body {
  padding-top: 0.2rem;
  margin-bottom: 0.3rem;
}
.playerModal .video-detail-list-view .am-list-body::after {
  opacity: 0;
}
.playerModal .msg-body .messageBoard {
  display: flex;
  flex-direction: column;
  margin-top: 0.2rem;
  padding-bottom: 0.2rem;
}
.playerModal .msg-body .messageBoard img {
  height: 0.8rem;
  width: 0.8rem;
  border-radius: 50%;
  border: 3px solid #fff;
  box-shadow: -0.5px -0.5px 5px #181717;
}
.playerModal .msg-body .messageBoard .love {
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  background: url(data:image/png;base64,) no-repeat center;
  background-size: 0.5rem 0.5rem;
}
.playerModal .msg-body .messageBoard .lovePress {
  display: block;
  width: 0.5rem;
  height: 0.5rem;
  background: url(data:image/png;base64,) no-repeat center;
  background-size: 0.5rem 0.5rem;
}
.playerModal .msg-body .messageBoards {
  padding-bottom: 0.2rem;
  width: 6.9rem;
}
.playerModal .msg-body .messageBoards .msgData {
  text-align: left;
  background: #e6e3e3;
  border-radius: 0.15rem;
  padding: 0.1rem;
  word-wrap: break-word;
}
