#home-video .am-tabs {
  top: 1rem;
}
#home-video .am-search-cancel {
  line-height: 0;
  height: auto;
}
#home-video .am-tabs-top .am-tabs-tab-bar-wrap {
  position: fixed;
  left: 0;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0);
  -webkit-transition: 1s;
  transition: 1s;
}
#home-video .am-tabs-default-bar-top .am-tabs-default-bar-nextpage,
#home-video .am-tabs-default-bar-bottom .am-tabs-default-bar-nextpage {
  background: none;
  background-color: rgba(0, 0, 0, 0);
}
#home-video .am-tabs-default-bar-top .am-tabs-default-bar-prevpage,
#home-video .am-tabs-default-bar-bottom .am-tabs-default-bar-prevpage {
  background: none;
  background-color: rgba(0, 0, 0, 0);
}
#home-video .am-tabs-default-bar-top {
  background-color: rgba(0, 0, 0, 0) !important;
}
#home-video .am-tabs-default-bar-tab {
  color: #fff;
  height: 0.5rem;
}
#home-video .am-tabs-default-bar-tab-active {
  color: #fff600;
  font-weight: 800;
}
#home-video .am-tabs-default-bar-underline {
  border: 1px #fff600 solid;
}
#home-video .am-list-view-scrollview {
  padding: 0.5rem 0 !important;
}
#home-video .am-list-body {
  padding: 0 0.2rem;
}
#home-video .am-list-header {
  padding: 0 !important;
}
.newVideoListView .list-view-section-body {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
